
@include font-face('pattaya', '#{$font-path}pattaya-regular/pattaya-regular');
@include font-face('montserrat', '#{$font-path}montserrat-regular/montserrat-regular');
@include font-face('montserrat', '#{$font-path}montserrat-light/montserrat-light', 300);
@include font-face('montserrat', '#{$font-path}montserrat-bold/montserrat-bold', 700);
@include font-face('montserrat', '#{$font-path}montserrat-extrabold/montserrat-extrabold', 800);
@include font-face('oswald', '#{$font-path}oswald-bold/oswald-bold', 700);

@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(~material-design-icons/iconfont/MaterialIcons-Regular.eot); /* For IE6-8 */
  src: local('Material Icons'),
       local('MaterialIcons-Regular'),
       url(~material-design-icons/iconfont/MaterialIcons-Regular.woff2) format('woff2'),
       url(~material-design-icons/iconfont/MaterialIcons-Regular.woff) format('woff'),
       url(~material-design-icons/iconfont/MaterialIcons-Regular.ttf) format('truetype');
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}

$ionicons-font-path: '~ionicons/fonts';
@import "~ionicons/scss/ionicons";
